"use client";
import React, { ChangeEvent } from 'react';
import TextInput from '@/ui/TextInput';

interface FilterInputsProps {
  filterParams: {
    stateCode: number;
    yearBuilt: number;
    functionalClass: number;
    owner: string;
  };
  handleFilterChange: (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => void;
}

const FilterInputs: React.FC<FilterInputsProps> = ({ filterParams, handleFilterChange }) => {
  return (
    <div className="grid grid-cols-4 gap-4">
      <TextInput
        label="State Code"
        placeholder="Enter State Code"
        value={filterParams.stateCode}
        onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'stateCode' } })}
      />
      <TextInput
        label="Year Built"
        placeholder="Enter Year Built"
        value={filterParams.yearBuilt}
        onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'yearBuilt' } })}
      />
      <TextInput
        label="Functional Class"
        placeholder="Enter Functional Class"
        value={filterParams.functionalClass}
        onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'functionalClass' } })}
      />
      <TextInput
        label="Owner"
        placeholder="Enter Owner Code"
        value={filterParams.owner}
        onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'owner' } })}
      />
    </div>
  );
};

export default FilterInputs;